import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { divIcon } from 'leaflet'

const CreateServiceLogic = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [serviceList, setserviceList] = useState([])
    const [selectRuleProvider, setRuleProvider] = useState({})
    const [userInput, setUserInput] = useState({
        serviceKeyword: '',
        ruleProvider: '',
        isFinancial: false,
        minimum: 0,
        maximum: 0
    })
    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    useEffect(() => {
        localStorage.setItem('useBMStoken', true)
        useJwt.getServiceList().then(res => {
            console.log(res)
            setserviceList(res.data)
            localStorage.setItem('useBMStoken', false)
        }).catch(err => {
            Error(err)
            console.log(err)
            localStorage.setItem('useBMStoken', false)
        })
    }, [])
    const onSubmit = (e) => {
        e.preventDefault()
        localStorage.setItem('useBMStoken', true)
        const { serviceKeyword, isFinancial, minimum, maximum} = userInput
        let { ruleProvider} = userInput
        ruleProvider = selectRuleProvider.value
        setPointRuleloading(true)
        useJwt.createServiceLogic({ serviceKeyword, ruleProvider, isFinancial, minimum, maximum}).then((response) => {
            setPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Error(error)
            console.log(error)
          })
    }
    return (
        <Fragment>
            <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allServiceLogics' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Add Service Logic</CardTitle>
                    
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                            <FormGroup>
                                <Label for="serviceKeyword">Service Type</Label>
                                <Select
                                    theme={selectThemeColors}
                                    maxMenuHeight={200}
                                    className='react-select'
                                    classNamePrefix='select'
                                    value={{ value: userInput.serviceKeyword, label: userInput.serviceKeyword ? userInput.serviceKeyword : 'select a service type'}}
                                    onChange={(selected) => {
                                        setUserInput({ ...userInput, serviceKeyword: selected.value })
                                    }}
                                    options={serviceList?.map(s => { return {value: s.serviceKeyword, label: s.serviceKeyword} })}
                                />
                            </FormGroup>
                        </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="minimum">MIN TXN Amount</Label>
                            <Input type="number"
                                name="minimum"
                                id='minimum'
                                value={userInput.minimum}
                                onChange={handleChange}
                                required
                                placeholder= '0'
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="maximum">MAX TXN Amount</Label>
                            <Input type="number"
                                name="maximum"
                                id='maximum'
                                value={userInput.maximum}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                            <FormGroup>
                                <Label for="ruleProvider">Charge Lookup</Label>
                                <Select
                                    theme={selectThemeColors}
                                    maxMenuHeight={200}
                                    className='react-select'
                                    classNamePrefix='select'
                                    value={{ value: selectRuleProvider.value, label: selectRuleProvider.label ? selectRuleProvider.label : 'select a type'}}
                                    onChange={(selected) => {
                                        setRuleProvider({ value: selected.value,  label: selected.label})
                                    }}
                                    options={[{value: 's', label: 'Sender'}, {value: 'r', label: 'Reciever'}]}
                                />
                            </FormGroup>
                        </Col>                
                        <Col sm="12" >
                            <FormGroup check>
                                <Input onChange={(e) => {
                                        if (e.target.checked) { 
                                            setUserInput({ ...userInput, isFinancial: true })
                                        } else {
                                            setUserInput({ ...userInput, isFinancial: false })
                                        }
                                    }
                                } type='checkbox' id='isFinancial' />
                                <Label for='isFinancial' check>
                                Is Financial?
                                </Label>
                            </FormGroup>
                        </Col>
                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateServiceLogic